.packs {
  padding: 56px 0 44px 0;

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__rating {
    margin-bottom: 127px;
    padding: 50px 122px 48px 69px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #F7F8FF;
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
  }

  &__progress {
    max-width: 482px;
    display: flex;
    align-items: center;
  }

  &__circular {
    position: relative;
    margin-right: 27px;
    width: 109px;
    height: 109px;
    min-width: 109px;
    min-height: 109px;
  }

  &__progress-inner {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 6;
    top: 50%;
    left: 50%;
    width: 80px;
    height: 80px;
    margin: -40px 0 0 -40px;
    background-color: var(--light-color);
    border-radius: 100%;
    font-weight: 400;
    font-size: 27px;
    line-height: 48px;
    text-align: center;
    color: var(--rating-color);
  }

  &__progress-bar {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #D3E0D7;
    border-radius: 100%;
    clip: rect(0px, 109px, 109px, 54.5px);
  }

  &__progress-progress {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    clip: rect(0px, 55px, 109px, 0px);
    background-color: var(--rating-color);
  }

  &__progress-bar--left {
    & > div {
      z-index: 1;
      animation: left 2s linear both;
    }
  }

  @keyframes left {
    100% {
      transform: rotate(180deg);
    }
  }

  &__progress-bar--right {
    z-index: 3;
    transform: rotate(180deg);

    & > div {
      animation: right 2s linear both;
      animation-delay: 2s;
    }
  }

  @keyframes right {
    100% {
      transform: rotate(160deg);
    }
  }

  &__progress-title {
    margin: 0;
    max-width: 443px;
    font-weight: 700;
    font-size: 36px;
    line-height: 48px;
    color: var(--title-color);
  }

  &__marks {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }

  &__stars {
    margin-bottom: 23px;
    margin-top: 14px;
    max-height: 35px;
    display: flex;
    align-items: center;
  }

  &__stars-number {
    margin: 0;
    font-weight: 700;
    font-size: 36px;
    line-height: 62px;
    color: var(dark-color);
  }

  &__marks-link {
    margin: 0;
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    text-decoration-line: underline;
    color: #424B5A;

    & img {
      margin-left: 4px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 44px;
    line-height: 62px;
    color: var(--title-color);
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 101px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-dark-color);
  }

  &__items {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__item {
    padding: 35px 60px;
    margin-bottom: 30px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: var(--light-color);
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 17px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;

    &:last-child {
      margin-bottom: 68px;
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
  }

  &__descr-title {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 400;
    font-size: 28px;
    line-height: 27px;
    color: var(--text-dark-color);
  }

  &__descr-bottom {
    display: flex;
    align-items: center;
  }

  &__descr-subtitle {
    margin: 0;
    margin-right: 28px;
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: #8F90A6;

    & img {
      margin-right: 5px;
    }
  }
}
